.chat-frame {
  flex: 1;
  align-self: stretch;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}

.hide {
  display: none;
}

.chat-frame .chat-header {
  flex: 0;
  align-items: center;
  align-self: stretch;
  background-color: #f9f9f9;
  border-bottom-style: solid;
  border-bottom-width: 1px;
  border-color: #e8e8e8;
  display: flex;
  gap: 12px;
  padding: 10px 32px;
  position: relative;
}

.chat-frame .chat-header-icons {
  height: 52px;
  width: 164px;
  display: flex;
  align-items: center;
  gap: 12px;
}

.chat-frame .chat-header-member-icons {
  display: flex;
  gap: 0;
}

.chat-frame .chat-header-member-icons .chat-header-icon {
  border: 2px solid;
  border-color: #ffffff;
  border-radius: 26px;
  height: 52px;
  min-width: 52px;
}

.chat-frame .chat-header-member-icons .chat-header-icon:nth-child(2) {
  margin-left: -26px;
}

.chat-frame .chat-header-member-icons .chat-header-icon:nth-child(3) {
  margin-left: -26px;
}

.chat-frame .dashed-icon-button {
  align-items: center;
  border: 1px dashed;
  border-color: #0f1217;
  border-radius: 18px;
  display: flex;
  flex-direction: column;
  height: 36px;
  width: 36px;
  justify-content: center;
  overflow: hidden;
}

.chat-frame .chat-header-title-row {
  align-items: center;
  display: flex;
  flex: 1;
  flex-direction: column;
  gap: 10px;
  padding: 12px;
  position: relative;
}

.chat-frame .chat-header-title {
  color: #0f1217;
  font-family: "Work Sans", Helvetica;
  font-size: 20px;
  font-weight: 600;
  letter-spacing: 0;
  line-height: normal;
  margin-top: -1px;
  position: relative;
  width: fit-content;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.chat-frame .chat-header-subtext {
  color: #666666;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0;
  line-height: 22px;
  position: relative;
  white-space: nowrap;
  width: fit-content;
}

.chat-frame .chat-header-menu {
  align-items: center;
  display: flex;
  gap: 12px;
  justify-content: flex-end;
  padding: 19px 12px;
  position: relative;
  width: 164px;
}

.chat-frame .chat-messages {
  align-self: stretch;
  flex: 1;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  gap: 12px;
  padding: 32px 32px;
  align-items: stretch;
}

.chat-frame .chat-footer {
  align-items: center;
  align-self: stretch;
  display: flex;
  gap: 12px;
  justify-content: center;
  padding: 0px 32px;
  position: relative;
}

.chat-frame .chat-footer-contents {
  align-items: center;
  border-color: #666666;
  border-top-style: solid;
  border-top-width: 2px;
  display: flex;
  flex: 1;
  flex-direction: column;
  gap: 16px;
  padding: 18px 0px 24px;
  position: relative;
}

.chat-frame .chat-text-box {
  align-self: stretch;
  color: #0f1217;
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 0;
  line-height: normal;
  white-space: nowrap;
  border: none;
}

.chat-frame .chat-text-box:focus{
  outline: none;
}

.chat-frame .chat-entry-menu {
  align-items: center;
  align-self: stretch;
  display: flex;
  gap: 12px;
  position: relative;
}

.chat-frame .chat-send-button {
  align-items: center;
  background-color: #0f1217;
  border-radius: 3px;
  display: flex;
  flex-direction: column;
  gap: 16px;
  justify-content: center;
  overflow: hidden;
  padding: 8px 16px;
  position: relative;
  width: fit-content;
}
